import { Validators, createFormValidation } from '@lemoncode/fonk';
import {
  myValidatorDay,
  myValidatorMonth,
  myValidatorYear,
} from './transfer.validator.custom';

const validationSchema = {
  field: {
    iban: [
      {
        validator: Validators.required,
        message: 'Campo requerido',
      },
      {
        validator: Validators.pattern,
        customArgs: {
          pattern:
            /[A-Z]\d{2}[ ]\d{4}[ ]\d{4}[ ]\d{4}[ ]\d{4}[ ]\d{4}|ES\d{22}/,
        },
        message: 'El IBAN no es correcto',
      },
    ],
    name: [
      {
        validator: Validators.required,
        message: 'Campo requerido',
      },
      {
        validator: Validators.pattern,
        customArgs: {
          pattern:
            /^([A-Za-z]{0}?[A-Za-z\']+[\s])+([A-Za-z]{0}?[A-Za-z\'])+[\s]?([A-Za-z]{0}?[A-Za-z\'])?$/,
        },
        message: 'Introduzca un nombre vlido',
      },
    ],
    amount: [
      {
        validator: Validators.required,
        message: 'Campo requerido',
      },
      {
        validator: Validators.pattern,
        customArgs: { pattern: /[0-9]/ },
        message: 'Introduzca slo caracteres numricos',
      },
    ],
    concept: [
      {
        validator: Validators.required,
        message: 'Campo requerido',
      },
      {
        validator: Validators.pattern,
        customArgs: {
          pattern: /[A-Za-z]{2}?[A-Za-z\']/,
        },
        message: 'Introduzca un concepto correcto',
      },
    ],
    day: [
      {
        validator: myValidatorDay,
      },
    ],
    month: [
      {
        validator: myValidatorMonth,
      },
    ],
    year: [
      {
        validator: myValidatorYear,
      },
    ],
    email: [
      {
        validator: Validators.required,
        message: 'Campo requerido',
      },
      {
        validator: Validators.email,
        message: 'Email no vlido',
      },
    ],
  },
};

export const formValidation = createFormValidation(validationSchema);
